// Inquiry Actions
export async function updateInquiryStatusAction(id: string, status: ClientInquiryStatus) {
  console.log(`Updating inquiry ${id} to status ${status} (simulated in-memory)`);
  const inquiryIndex = clientInquiries.findIndex(inq => inq.id === id);
  if (inquiryIndex !== -1) {
    clientInquiries[inquiryIndex].status = status;
    console.log(`Inquiry ID ${id} status updated to ${status}.`);
  } else {
    console.warn(`updateInquiryStatusAction: Inquiry with ID ${id} not found.`);
  }
  revalidatePath("/admin/inquiries");
  revalidatePath("/admin"); 
}

export async function submitQuoteRequestAction(data: QuoteRequestFormValues) {
  console.log("Server action: submitQuoteRequestAction called with data:", data);
  
  const newInquiry: ClientInquiry = {
    id: `inq-${Date.now()}-${Math.random().toString(36).substring(2, 7)}`,
    name: data.name,
    email: data.email,
    company: data.company || undefined, // Ensure company is optional
    serviceRequested: data.service,
    details: data.projectDetails,
    date: new Date().toISOString(),
    status: 'New',
  };
  clientInquiries.unshift(newInquiry); 
  
  console.log("New inquiry added to clientInquiries (simulated):", newInquiry);
  console.log("Current clientInquiries count:", clientInquiries.length);

  revalidatePath("/admin/inquiries");
  revalidatePath("/admin");
  
  // Redirect to the inquiries page so the user sees the new inquiry.
  // Optionally, add a query param to show a success message on that page.
  redirect(`/admin/inquiries?newInquiryId=${newInquiry.id}&status=success`);
}
